import React, { useState, useEffect } from "react";
import { Button, InputGroup, Intent, Card, Elevation, HTMLSelect } from "@blueprintjs/core";
import { BLUEPRINT_COLORS, migrateRules } from "./logic";
import IconPicker from "./IconPicker";
const MagicTagSettings = ({ extensionAPI, onUpdate }) => {
    const [rules, setRules] = useState([]);
    const [theme, setTheme] = useState("blueprint");
    useEffect(() => {
        const savedRules = extensionAPI.settings.get("rules");
        if (savedRules) {
            setRules(migrateRules(savedRules));
        }
        const savedTheme = extensionAPI.settings.get("theme");
        if (savedTheme) {
            setTheme(savedTheme);
        }
    }, [extensionAPI]);
    const saveAll = (newRules, newTheme) => {
        setRules(newRules);
        setTheme(newTheme);
        extensionAPI.settings.set("rules", newRules);
        extensionAPI.settings.set("theme", newTheme);
        onUpdate(newRules, newTheme);
    };
    const addRule = () => {
        const newRules = [...rules, { tag: "", icon: theme === "feather" ? "plus" : "add", color: BLUEPRINT_COLORS[0].name }];
        saveAll(newRules, theme);
    };
    const removeRule = (index) => {
        const newRules = rules.filter((_, i) => i !== index);
        saveAll(newRules, theme);
    };
    const updateRule = (index, updates) => {
        const newRules = rules.map((rule, i) => i === index ? { ...rule, ...updates } : rule);
        saveAll(newRules, theme);
    };
    const updateTheme = (newTheme) => {
        saveAll(rules, newTheme);
    };
    const exportSettings = () => {
        const data = { rules, theme };
        const blob = new Blob([JSON.stringify(data, null, 2)], { type: "application/json" });
        const url = URL.createObjectURL(blob);
        const link = document.createElement("a");
        link.href = url;
        link.download = "magictags-settings.json";
        link.click();
        URL.revokeObjectURL(url);
    };
    const importSettings = (e) => {
        const file = e.target.files?.[0];
        if (!file)
            return;
        const reader = new FileReader();
        reader.onload = (event) => {
            try {
                const content = event.target?.result;
                const data = JSON.parse(content);
                if (data.rules && Array.isArray(data.rules)) {
                    saveAll(migrateRules(data.rules), data.theme || "blueprint");
                }
                else {
                    console.error("Invalid settings file format.");
                }
            }
            catch (error) {
                console.error("Error parsing settings file:", error);
            }
        };
        reader.readAsText(file);
        e.target.value = "";
    };
    const uniqueColorNames = Array.from(new Set(BLUEPRINT_COLORS.map(c => c.name)));
    return (React.createElement("div", { style: { padding: "10px" } },
        React.createElement("div", { style: { marginBottom: "20px", display: "flex", justifyContent: "space-between", alignItems: "flex-end", flexWrap: "wrap", gap: "10px" } },
            React.createElement("div", { style: { maxWidth: "200px", flex: "1 1 auto" } },
                React.createElement("label", { className: "bp3-label" },
                    "Icon Theme",
                    React.createElement(HTMLSelect, { value: theme, onChange: (e) => updateTheme(e.target.value), options: [
                            { label: "Blueprint", value: "blueprint" },
                            { label: "Feather (Lucide)", value: "feather" },
                        ], fill: true }))),
            React.createElement("div", { style: { display: "flex", gap: "10px", paddingBottom: "5px" } },
                React.createElement(Button, { icon: "import", onClick: () => document.getElementById("import-settings-input")?.click() }, "Import"),
                React.createElement("input", { id: "import-settings-input", type: "file", accept: ".json", style: { display: "none" }, onChange: importSettings }),
                React.createElement(Button, { icon: "export", onClick: exportSettings }, "Export"))),
        rules.map((rule, index) => (React.createElement(Card, { key: index, elevation: Elevation.ZERO, style: {
                display: "flex",
                flexWrap: "wrap",
                gap: "10px",
                marginBottom: "10px",
                alignItems: "flex-end",
                padding: "10px",
                background: "transparent",
            } },
            React.createElement("div", { style: { flex: "1 1 80px" } },
                React.createElement("label", { className: "bp3-label", style: { marginBottom: "5px", display: "block" } },
                    "Tag Name",
                    React.createElement(InputGroup, { placeholder: "e.g. todo", value: rule.tag, onChange: (e) => updateRule(index, { tag: e.target.value }), style: { width: "100%", minWidth: "100%" } }))),
            React.createElement("div", { style: { width: "40px" } },
                React.createElement("label", { className: "bp3-label", style: { marginBottom: "5px", display: "block" } },
                    "Icon",
                    React.createElement(IconPicker, { currentIcon: rule.icon, theme: theme, onChange: (hex) => updateRule(index, { icon: hex }) }))),
            React.createElement("div", { style: { width: "120px" } },
                React.createElement("label", { className: "bp3-label", style: { marginBottom: "5px", display: "block" } },
                    "Color",
                    React.createElement(HTMLSelect, { value: rule.color, onChange: (e) => updateRule(index, { color: e.target.value }), options: uniqueColorNames.map((name) => ({ label: name, value: name })), fill: true }))),
            React.createElement("div", { style: { paddingBottom: "5px" } },
                React.createElement(Button, { icon: "trash", intent: Intent.DANGER, minimal: true, onClick: () => removeRule(index) }))))),
        React.createElement(Button, { icon: "add", intent: Intent.PRIMARY, onClick: addRule, style: { marginTop: "10px" } }, "Add Magic Tag Rule")));
};
export default MagicTagSettings;
